import type { Fetcher } from "@remix-run/react";
import * as React from "react";
import { Transition } from "@headlessui/react";
import {
  ExclamationCircleIcon,
  CheckCircleIcon,
} from "@heroicons/react/24/outline";
import XMark from "./icons/XMark";

interface Props {
  status: Fetcher;
  classNames?: string;
}

export default function Notification({ status, classNames }: Props) {
  const [show, setShow] = React.useState<boolean>(false);

  React.useEffect(() => {
    setShow(true);

    const timer = setTimeout(() => {
      setShow(false);
    }, 2500);

    return () => {
      clearTimeout(timer);
    };
  }, []);

  if (!show || !status) return null;

  return (
    <div
      aria-live="assertive"
      className={`pointer-events-none fixed bottom-0 flex items-center px-4 py-6 sm:p-6 ${classNames}`}
    >
      <div className="flex w-96 flex-col items-center space-y-4">
        <Transition
          show={show}
          as={React.Fragment}
          enter="transform ease-out duration-300 transition"
          enterFrom="translate-y-2 opacity-0 sm:translate-y-0 sm:translate-x-2"
          enterTo="translate-y-0 opacity-100 sm:translate-x-0"
          leave="transition ease-in duration-100"
          leaveFrom="opacity-100"
          leaveTo="opacity-0"
        >
          <div className="pointer-events-auto w-full max-w-sm overflow-hidden rounded-lg bg-white shadow-lg ring-1 ring-black ring-opacity-5">
            <div className="p-4">
              <div className="flex items-start">
                <div className="flex-shrink-0">
                  {status?.data?.error ? (
                    <ExclamationCircleIcon
                      className="h-6 w-6 text-red-800"
                      aria-hidden="true"
                    />
                  ) : (
                    <CheckCircleIcon
                      className="h-6 w-6 text-green-400"
                      aria-hidden="true"
                    />
                  )}
                </div>
                <div className="ml-3 w-0 flex-1 pt-0.5">
                  <p className="mt-1 text-sm text-gray-500">
                    {status?.data?.message || status?.data?.errorMessage}
                  </p>
                </div>
                <div className="ml-4 flex flex-shrink-0">
                  <button
                    type="button"
                    className="inline-flex rounded-md bg-white text-gray-400 hover:text-gray-500 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2"
                    onClick={() => setShow(false)}
                  >
                    <span className="sr-only">Close</span>
                    <XMark className="h-5 w-5" aria-hidden="true" />
                  </button>
                </div>
              </div>
            </div>
          </div>
        </Transition>
      </div>
    </div>
  );
}
